@font-face {
  font-family: "Suisse Intl";
  font-weight: 400;
  font-style: normal; }

@font-face {
  font-family: "Suisse Intl";
  font-weight: 500;
  font-style: normal; }

@font-face {
  font-family: "Suisse Intl";
  font-weight: 700;
  font-style: normal; }

* {
  box-sizing: border-box; }

html {
  height: 100%; }

body {
  font-family: "Suisse Intl", "Arial", sans-serif;
  font-size: 16px;
  line-height: 24px;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  margin: 0;
  width: 100%;
  min-width: 320px;
  min-height: 100%;
  color: #323b47; }

.header,
.footer {
  flex-shrink: 0; }

main {
  flex-grow: 1; }

a {
  color: inherit;
  text-decoration: none; }

img {
  display: block;
  max-width: 100%;
  height: auto; }

video {
  display: block;
  width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  overflow: hidden; }

.container {
  max-width: 1920px;
  width: 100%;
  padding: 0 145px;
  margin: 0 auto; }
  @media (max-width: 1279px) {
    .container {
      padding: 85px; } }
  @media (max-width: 767px) {
    .container {
      padding: 15px; } }

.btn {
  font-size: 14px;
  line-height: 20px;
  border: none;
  margin: 0;
  padding: 0;
  width: auto;
  color: #ffffff;
  cursor: pointer;
  text-align: center;
  vertical-align: middle;
  background-color: #39baf0;
  border-radius: 3px; }

.main-title {
  font-size: 64px;
  line-height: 74px; }
  @media (max-width: 1279px) {
    .main-title {
      font-size: 48px;
      line-height: 48px; } }
  @media (max-width: 767px) {
    .main-title {
      font-size: 28px;
      line-height: 34px; } }

.page-footer {
  position: relative;
  width: 100%;
  background-color: #f5f7f9; }
  .page-footer__wrap {
    padding-top: 76px;
    padding-bottom: 90px; }
  .page-footer__nav {
    display: flex;
    justify-content: space-between; }
  .page-footer__logo {
    display: block;
    margin-bottom: 146px; }
  .page-footer__contacts {
    font-size: 16px;
    line-height: 24px;
    max-width: 200px; }
    .page-footer__contacts dl {
      margin: 0; }
    .page-footer__contacts dt {
      position: relative;
      font-weight: 700;
      margin-bottom: 12px;
      text-transform: uppercase; }
      .page-footer__contacts dt::before {
        content: "";
        position: absolute;
        left: -28px;
        top: 4px; }
      .page-footer__contacts dt:nth-of-type(1)::before {
        width: 16px;
        height: 16px;
        background-image: url("../img/icon-phone.svg");
        background-repeat: no-repeat; }
      .page-footer__contacts dt:nth-of-type(2)::before {
        top: 0;
        width: 14px;
        height: 20px;
        background-image: url("../img/icon-pin.svg");
        background-repeat: no-repeat; }
      .page-footer__contacts dt:nth-of-type(3)::before {
        width: 19px;
        height: 15px;
        background-image: url("../img/icon-mail.svg");
        background-repeat: no-repeat; }
    .page-footer__contacts dd {
      margin: 0;
      margin-bottom: 52px;
      font-weight: 400;
      color: #5f6f85; }
      .page-footer__contacts dd:first-of-type {
        line-height: 19px; }
      .page-footer__contacts dd:nth-of-type(2) {
        margin-bottom: 42px; }
      .page-footer__contacts dd:last-of-type {
        line-height: 21px; }

.social p {
  margin: 0; }

.social__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  margin-bottom: 35px; }

.social__item {
  margin-right: 30px; }

.social__link {
  display: block;
  background-repeat: no-repeat; }
  .social__link--whatsapp {
    width: 26px;
    height: 26px;
    background-image: url("../img/icon-whatsapp.svg"); }
  .social__link--insta {
    width: 28px;
    height: 28px;
    background-image: url("../img/icon-instagram.svg"); }
  .social__link--fb {
    width: 14px;
    height: 27px;
    background-image: url("../img/icon-facebook.svg"); }

.map {
  height: 653px;
  margin-bottom: 8px;
  overflow: hidden;
  background-color: #f5f7f9; }
  .map img {
    max-width: 100%;
    height: auto; }

.page-footer__modal {
  position: absolute;
  left: 50%;
  bottom: 22.5%;
  width: 700px;
  min-height: 700px;
  padding: 42px 95px;
  background-color: #ffffff;
  transform: translate(-50%, 0); }
  .page-footer__modal label {
    position: absolute;
    font-weight: 500;
    font-size: 15px;
    line-height: 14px;
    letter-spacing: 0.02em;
    opacity: 0.8; }

.modal__title {
  font-size: 52px;
  line-height: 61px;
  font-weight: 400;
  margin: 0;
  margin-bottom: 13px;
  text-align: center; }

.modal__text {
  font-size: 18px;
  line-height: 28px;
  margin: 0;
  margin-bottom: 43px;
  text-align: center; }

.modal__first-line {
  display: flex;
  justify-content: space-between;
  margin-bottom: 40px; }

.modal__attachment label {
  display: block;
  position: relative;
  padding-left: 36px; }
  .modal__attachment label::before {
    content: "";
    position: absolute;
    width: 26px;
    height: 21px;
    left: 0;
    top: -3px;
    background-image: url("../img/icon-cloud.svg");
    background-repeat: no-repeat; }

.modal__agreement label {
  font-size: 12px;
  line-height: 18px;
  max-width: 490px;
  padding-left: 50px; }

.modal__agreement input {
  display: none; }

.modal button {
  font-size: 15px;
  line-height: 57px;
  display: block;
  margin: 40px auto 23px;
  width: 319px;
  height: 57px;
  text-transform: uppercase; }

form p {
  margin: 0; }

form input {
  width: 100%;
  min-width: 230px;
  height: 38px;
  padding: 0;
  border: none;
  border-bottom: 1px solid rgba(50, 59, 71, 0.6); }
  form input::placeholder {
    display: block;
    margin-bottom: 5px; }

form textarea {
  width: 100%;
  min-height: 110px;
  margin-top: 37px;
  margin-bottom: 10px;
  padding: 10px 15px;
  border: 1px solid rgba(50, 59, 71, 0.6); }
